const ButtonType = [
  "primary",
  "success",
  "info",
  "warning",
  "danger",
  "text"
];
const ButtonSize = ["midium", "small", "mini"];
const buttonProps = {
  type: {
    type: String,
    validator(value) {
      return ButtonType.includes(value);
    }
  },
  size: {
    type: String,
    validator(value) {
      return ButtonSize.includes(value);
    }
  },
  plain: Boolean,
  round: Boolean,
  disabled: Boolean
};
const buttonEmits = {
  click: (evt) => evt instanceof MouseEvent
};
export {
  ButtonSize,
  ButtonType,
  buttonEmits,
  buttonProps
};
