function useSwitchViewPoint(viewType, viewer) {
  if (!viewer) {
    console.error("viewer is undefined");
    return false;
  }
  var scene = viewer.scene;
  switch (viewType) {
    case "3d":
      scene.morphTo3D(0);
      break;
    case "2d":
      scene.morphTo2D(0);
      break;
    case "2.5d":
      scene.morphToColumbusView(0);
      break;
  }
}
export {
  useSwitchViewPoint
};
